<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <div class="temp-item-inlineblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->
      <div class="temp-item-inlineblock">
        <el-select v-model="adCampaignsGet.campaign_type" placeholder="推广计划类型" style="width:180px;">
          <el-option v-for="item in campaignTypeList" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adCampaignsGet.product_type" placeholder="标的物类型">
          <el-option v-for="item in productTypeList" :key="item.value" :label="item.name" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="configured_status_default" filterable placeholder="请选择" @change="changeConfiguredStatus">
          <el-option v-for="item in configured_status" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="请输入计划名查询" @change="changeCampaign_name"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="getList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="exportExcel">导出为excel</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <a class="new-plan" href="/marketing/#/advertising/campaign/new">新建计划</a>
      </div>
      <div class="temp-item-block">
        <el-button type="primary" @click="handleListRemove">删除</el-button>
        <el-dropdown split-button type="primary" @click="handleBatchChangeStatus" @command="handleCommand">
          修改状态
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item :command="key" :key="key" v-for="(val, key) in configured_status_list">{{ val }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>

        <el-popover ref="popover" placement="bottom-start" visible-arrow="false" width="660" v-model="visible">
          
          <div class="nav-pop">
          <div class="datepop">
            <div class="selectarea">
            <form onsubmit="return false;" action="return false;">
              <ul class="select-classify clearfix" id="select-classify">
                <li class="classify-item require" v-for="(checkItem,checkKey) in checkBox" :disabled="checkItem.disabled === true">
                  <div class="tit">
                  <strong><el-checkbox :indeterminate="isIndeterminate[checkKey]" v-model="checkBoxAll[checkKey]" :disabled="checkItem.disabled === true" @change="handleCheckAllChange(checkKey)">{{checkItem.name}}</el-checkbox></strong>
                  </div>
                  <ul class="checkitem clearfix">
                <el-checkbox-group v-model="checkList[checkKey]">
                    <li class="checkitem-li" v-for="(subItem,subKey) in checkItem.sub">
                      <el-checkbox  :label="subKey" :disabled="checkItem.disabled === true">{{subItem}}</el-checkbox>
                    </li>
                </el-checkbox-group>
                  </ul>
                </li>
              </ul>
            </form>
            </div>
          </div>
          </div>
          <div style="text-align: right; margin-top: 10px">
            <el-button size="mini" type="text" @click="checkBoxCancel">取消</el-button>
            <el-button type="primary" size="mini" @click="checkBoxSubmit">确定</el-button>
          </div>
        </el-popover>
        <el-button v-popover:popover>自定义列</el-button>
      </div>

      
    </div>
    <div class="table-wrap" v-loading="loading">
      <el-table show-overflow-tooltip :summary-method="getSummaries" show-summary ref="multipleTable" @selection-change="handleSelectionChange" @sort-change="changeSort" :data="listData" 
      style="width: 100%">
        <el-table-column type="selection">
        </el-table-column>
        <el-table-column prop="campaign_name" label="计划名称">
          <template slot-scope="scope">
            <span @click="handleGetAdgroups(scope.row.campaign_id)" style="margin-left: 10px;color:#2b7ed1;cursor: pointer;">{{ scope.row.campaign_name }}</span>
            <i class="el-icon-edit" @click="handleListCampaignNameChange(scope.row)"></i>
          </template>
        </el-table-column>
        <el-table-column prop="campaign_type_name" label="计划类型" v-if="checkItems('campaign_type_name')" sortable>
        </el-table-column>
        <el-table-column prop="campaign_id" label="ID"  v-if="checkItems('campaign_id')" sortable>
        </el-table-column>


        <el-table-column v-for="(item, index) in checkBox[2]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column v-for="(item, index) in checkBox[3]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column v-for="(item, index) in checkBox[4]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column key="daily_budget" label="日限额(元/天)" v-if="checkItems('daily_budget')">
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.daily_budget / 100 }}</span>
            <i class="el-icon-edit" @click="handleListDailyBudgetChange(scope.row)"></i>
          </template>
        </el-table-column>
        <el-table-column prop="budget_reach_date" label="日限额到达日期" :formatter="formatReachDate" v-if="checkItems('budget_reach_date')" sortable>
        </el-table-column>
        <el-table-column prop="speed_mode_name" label="速度模式" v-if="checkItems('speed_mode_name')" sortable>
        </el-table-column>
        <el-table-column prop="product_type_name" label="标的物类型" v-if="checkItems('product_type_name')" >
        </el-table-column>
        <el-table-column prop="created_time" label="创建时间" v-if="checkItems('created_time')" sortable>
        </el-table-column>
        <el-table-column prop="last_modified_time" label="修改时间" v-if="checkItems('last_modified_time')" sortable>
        </el-table-column>
        <el-table-column prop="configured_status" label="状态" >
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.configured_status === 'AD_STATUS_NORMAL' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="address" label="预警" sortable>
        </el-table-column> -->
        <el-table-column prop="address" label="操作" sortable>
          <template slot-scope="scope">
            <el-button @click="handleEditClick(scope.row.campaign_id)" type="text" size="small">编辑</el-button>
            <el-button @click="handleCopyClick(scope.row.campaign_id)" type="text" size="small">复制</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[20, 50, 100]" :total="page_info.total_number" :page-size="page_info.page_size" layout="total, sizes, prev, pager, next, jumper">
        </el-pagination>
      </div>
    </div>

  </div>

</template>

<script>
export default {
  created: function() {
    if (this.account_id) {
      this.getAd_Campaigns(this.adCampaignsGet)
    }
    this.getCommonEnum()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.getAd_Campaigns(this.adCampaignsGet)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    }
  },
  data() {
    return {
      visible: false,
      checkBox: [{
        'name' : '必选项',
        'disabled' : true,
        'sub' : {
          'campaign_name':'计划名称',
          'operation':'操作',
          }
      },
      {
        'name' : '属性',
        'disabled' : false,
        'sub' : {
          'campaign_type_name':'计划类型',
          'product_type_name':'标的物类型',
          'campaign_id':'计划ID',
          'status':'状态',
          'daily_budget':'限额',
          'budget_reach_date':'限额到达日期',
          'speed_mode_name':'速度模式',
          'created_time':'创建时间',
          'last_modified_time':'修改时间'
          }
      },
      {
        'name' : '效果',
        'disabled' : false,
        'sub' : {
          'impression':'曝光量',
          'click':'点击量',
          'ctr':'点击率',
          'cpc':'点击均价(元)',
          'conversion':'安装量(转化量)',
          'clicktraderate':'安装率(转化率)',
          'cpt':'安装(转化)均价(元)',
          'cost':'花费(元)',
          }
      },
      {
        'name' : 'APP转化效果',
        'disabled' : false,
        'sub' : {
          'download':'下载量',
          'downloadrate':'下载率',
          'app_installation':'安装量',
          'cost_per_download':'下载均价',
          'activation':'激活总量',
          'activated_rate':'下载激活率',
          'click_activated_rate':'点击激活率',
          'activated_price':'激活均价(元)',
          'register': 'APP注册量',
          'app_payment_count': '付费行为量',
          'order_placement': '下单量',
          'app_payment_amount': '付费总金额(分)',
          'reservation': '表单预约量',
          }
      },
      {
        'name' : '社交',
        'disabled' : false,
        'sub' : {
          'follow': '关注量',
          'like_or_comment': '点赞量',
          'like_or_comment': '评论量',
          'share': '转发量',
          'image_click': '图片点击量',
          }
      },
      ],
      checkList: [['campaign_name','operation'],['campaign_type_name','product_type_name','daily_budget'],['impression','click','ctr','cpc','cost'],['activation'],[]],
      originCheckList: [['campaign_name','operation'],['campaign_type_name','product_type_name','daily_budget'],['impression','click','ctr','cpc','cost'],['activation'],[]],
      isIndeterminate: [false,false,false,false,false],
      checkBoxAll: [true,false,false,false,false],
      multipleSelection: [], // 批量 rows
      configured_status_command: '', //批量绑定状态
      configured_status_list: {}, // 批量状态
      dialogVisible: false,
      dialogVisibleText: '',
      adCampaignsGet: {
        page: 1,
        page_size: 20,
        campaign_id: '',
        configured_status: '',
        campaign_name: '',
        campaign_type: '',
        product_type: '',
        report: 1,
        start_date: '',
        end_date: '',
        sort_field: '',
        sort_type: ''
      },
      productTypeList: [{ value: '', name: '标的物类型' }],
      campaignTypeList: [{ value: '', label: '计划类型' }],
      loading: true,
      pickerDate: [new Date(), new Date()],
      pickerOptions2: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      },
      configured_status: [],
      configured_status_default: '',
      listData: [],
      sumData: [],
      page_info: {
        page: 1,
        page_size: 10,
        total_number: 0,
        total_page: 0
      }
    }
  },
  mounted() {
    //console.log(this.listData)
  },
  methods: {
    handleCheckAllChange(val) {
      this.checkList[val] = this.checkBoxAll[val] ? Object.keys(this.checkBox[val]['sub']) : []
    },
    checkBoxCancel(){
      this.checkList = JSON.parse(JSON.stringify(this.originCheckList))
      this.visible = false
    },
    checkBoxSubmit(){
      this.originCheckList = JSON.parse(JSON.stringify(this.checkList))
      this.visible = false
    },
    checkItems(val){
      var arr = this.checkList[1].concat(this.checkList[2]);
      arr = arr.concat(this.checkList[3]);
      arr = arr.concat(this.checkList[4]);
      if(arr.indexOf(val) !== -1){
        return true
      }else{
        return false
      }
    },
    changeSort(val){
      if(val.order && val.column.sortable == 'custom'){
        this.adCampaignsGet.sort_field = val.prop
        this.adCampaignsGet.sort_type = val.order
        this.getAd_Campaigns(this.adCampaignsGet)
      }
    },
    // 批量修改状态--请求
    campaignsBatchChangeStatus(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/batchChange', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getAd_Campaigns(that.adCampaignsGet) // 刷新计划列表
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 批量修改状态
    handleBatchChangeStatus() {
      var that = this,
        rows = this.multipleSelection,
        configured_status = this.configured_status_command
      if (configured_status) {
        if (rows && rows.length > 0) {
          var ids = []
          for (var i in rows) {
            ids.push(rows[i]['campaign_id'])
          }
          var params = {
            ids: ids,
            configured_status: configured_status
          }
          that.campaignsBatchChangeStatus(params)
        } else {
          that.$message('请选择批量修改状态的记录')
        }
      } else {
        that.$message('请选择批量修改的状态')
      }
    },
    // 选择批量修改的状态
    handleCommand(val) {
      this.configured_status_command = val
    },
    // 批量删除
    campaignsBatchDelete(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/batchDelete', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getAd_Campaigns(that.adCampaignsGet) // 刷新计划列表
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 批量删除计划
    handleListRemove() {
      var that = this,
        rows = this.multipleSelection
      if (rows && rows.length > 0) {
        var ids = [],
          isConfigured = false
        for (var i in rows) {
          ids.push(rows[i]['campaign_id'])
          //console.log(rows[i]['configured_status'])
          if (rows[i]['configured_status'] == 'AD_STATUS_NORMAL') {
            isConfigured = true
            break
          }
        }

        if (isConfigured) {
          that.$message('只有暂停的活动才能被删除')
          return false
        }

        this.$confirm('是否确认批量删除记录, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(() => {
            var params = {
              ids: ids
            }
            that.campaignsBatchDelete(params) // 批量删除请求
          })
          .catch(() => {})
      } else {
        that.$message('请选择需要删除的记录')
      }
    },
    //  批量 rows
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    formatReachDate(row, column) {
      if (row.budget_reach_date == 0) {
        return '---'
      } else {
        return row.budget_reach_date
      }
    },
    unitConversion(row, column) {
      return row.daily_budget / 100
    },
    // 计划 -- 查看所属广告组
    handleGetAdgroups(campaign_id) {
      this.$router.push('/advertising/adGroup/sub/' + campaign_id)
    },
    // 操作 -- 编辑
    handleEditClick(campaign_id) {
      this.$router.push('/advertising/campaign/edit/' + campaign_id)
    },
    // 操作 -- 复制
    handleCopyClick(campaign_id) {
      this.$router.push('/advertising/campaign/copy/' + campaign_id)
    },
    // 修改计划名
    handleListCampaignNameChange(row) {
      var oldval = row.bid_amount
      var that = this
      this.$prompt('请输入推广计划名称', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^[^/\\'\&]+$/,
        inputErrorMessage: '请输入有效值'
      })
        .then(({ value }) => {
          that.setCampaignsUpdate(row, 'campaign_name', value, oldval)
          row.campaign_name = value
        })
        .catch(() => {
        })
    },
    // 修改日限额
    handleListDailyBudgetChange(row) {
      var oldval = row.daily_budget / 100
      var that = this
      this.$prompt('请输入日限额(元 / 天)', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^([1-9]\d*)+(.\d{1,2})?$|^0\.\d{1,2}$/,
        inputErrorMessage: '请输入有效值'
      })
        .then(({ value }) => {
          if(value - oldval >= 50 || oldval - value >= 50){
            that.setCampaignsUpdate(row, 'daily_budget', value, oldval*100 )
            row.daily_budget = value * 100
          }else{
            that.$message.error('每次修改幅度不能低于50元')
          }
        })
        .catch(() => {
        })
    },
    //导出execel
    exportExcel() {
      var params = this.adCampaignsGet
      window.location.href =
        '/Admin/Marketing_Campaigns/export?page=' +
        params.page +
        '&page_size=' +
        params.page_size +
        '&campaign_id=' +
        params.campaign_id +
        '&configured_status=' +
        params.configured_status +
        '&campaign_name=' +
        params.campaign_name +
        '&start_date=' +
        params.start_date +
        '&end_date=' +
        params.end_date +
        '&account_id=' +
        this.account_id
    },
    // 公共枚举
    getCommonEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            //console.log('respData.list:', respData.data.list)
            that.configured_status_list = respData.data['AD_STATUS']
            var adStatus = respData.data['AD_STATUS']
            that.configured_status.push({ value: '', label: '所有状态' })
            for (var key in adStatus) {
              var obj = new Object()
              obj.value = key
              obj.label = adStatus[key]
              that.configured_status.push(obj)
            }
            for (var key in respData.data['PRODUCT_TYPE']) {
              var obj = new Object()
              obj.value = key
              obj.name = respData.data['PRODUCT_TYPE'][key]['name']
              that.productTypeList.push(obj)
            }
            for (var key in respData.data['CAMPAIGN_TYPE']) {
              var obj = new Object()
              obj.value = key
              obj.label = respData.data['CAMPAIGN_TYPE'][key]['name']
              that.campaignTypeList.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    changeConfiguredStatus(val) {
      this.adCampaignsGet.configured_status = val
    },
    changeCampaign_name(val) {
      this.adCampaignsGet.campaign_name = val
    },
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.adCampaignsGet.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.adCampaignsGet.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
      this.getAd_Campaigns(this.adCampaignsGet)
    },
    // 修改状态
    handleListStatusChange(row) {
      var current_status = row.configured_status
      if (row.configured_status === 'AD_STATUS_NORMAL') {
        row.configured_status = 'AD_STATUS_SUSPEND'
      } else {
        row.configured_status = 'AD_STATUS_NORMAL'
      }
      var that = this
      this.$axios
        .get('/Admin/Marketing_Campaigns/update', {
          params: {
            account_id: this.account_id,
            campaign_id: row.campaign_id,
            configured_status: row.configured_status
          }
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row.configured_status = current_status
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row.configured_status = current_status
        })
    },
    // 分页
    handleSizeChange(val) {
      this.adCampaignsGet.page_size = val
      this.getAd_Campaigns(this.adCampaignsGet)
      //console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.adCampaignsGet.page = val
      this.getAd_Campaigns(this.adCampaignsGet)
      //console.log(`当前页: ${val}`)
    },
    setCampaignsUpdate(row, key, value, oldval) {
      var that = this
      var params = new Object()
      params['campaign_id'] = row.campaign_id
      params[key] = value
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/update', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row[key] = oldval
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row[key] = oldval
        })
    },
    // 查询  推广计划 -- 列表
    getList() {
      //console.log('this.adCampaignsGet:', this.adCampaignsGet)
      this.adCampaignsGet.page = 1
      // this.adCampaignsGet.page_size = 10
      this.getAd_Campaigns(this.adCampaignsGet)
    },
    // 推广计划 -- 列表请求
    getAd_Campaigns: function(adParams) {
      var that = this
      that.loading = true
      adParams.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Campaigns/get', {
          params: adParams
        })
        .then(function(response) {
          //console.log(response)
          var respData = response.data
          if (respData.code == 0) {
            //console.log('respData.list:', respData.data.list)
            if (respData.data.list || respData.data.page_info) {
              that.listData = respData.data.list
              that.sumData = respData.data.sum
              console.log(that.sumData)
              that.page_info = respData.data.page_info
              //console.log('that.page_info:', that.page_info)
            }
          } else {
            // that.$message(respData.msg)
          }
          that.loading = false
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          that.loading = false
        })
    },
    getSummaries(param) {
      var arr = this.checkList[0].concat(this.checkList[1]);
      const { columns, data } = param;
      const sums = [];
      const itemKey = [];
      columns.forEach((column, index) => {
        itemKey[column.property] = index
        if (index === 1) {
          sums[index] = '总计';
          return;
        }
        if(arr.indexOf(column.property) !== -1){
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
        } else {
          sums[index] = '';
        }
      });
      sums[itemKey['ctr']] = this.sumData['ctr']
      sums[itemKey['cpc']] = this.sumData['cpc']
      sums[itemKey['clicktraderate']] = this.sumData['clicktraderate']
      sums[itemKey['cpt']] = this.sumData['cpt']
      sums[itemKey['activated_price']] = this.sumData['activated_price']
      // if(sums[itemKey['impression']] !=0){
      //   sums[itemKey['ctr']] = (sums[itemKey['click']] / sums[itemKey['impression']] *100).toFixed(2)  + '%'
      // }
      // if(sums[itemKey['click']] !=0){
      //   sums[itemKey['cpc']] = (sums[itemKey['cost']] / sums[itemKey['click']]).toFixed(2)
      //   sums[itemKey['clicktraderate']] = (sums[itemKey['conversion']] / sums[itemKey['click']]).toFixed(2)
      // }
      // if(sums[itemKey['conversion']] !=0){
      //   sums[itemKey['cpt']] = (sums[itemKey['cost']] / sums[itemKey['conversion']]).toFixed(2)
      // }
      // if(sums[itemKey['activation']] !=0){
      //   sums[itemKey['activated_price']] = (sums[itemKey['cost']] / sums[itemKey['activation']]).toFixed(2)
      // }

      return sums;
    }
  }
}
</script>


<style scoped>
.new-plan {
  text-decoration: none;
  display: inline-block;
  color: #fff;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #409eff;
  border: 1px solid #409eff;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
  margin: 0;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  font-weight: 500;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.tuiguang-toolbar .nav-pop {
    display: none;
    position: absolute;
    top: 28px;
    left: -1px;
    background: #fff;
    border: 1px solid #dbdee1;
    min-width: 280px;
}
.datepop {
    width: 670px;
    overflow: hidden;
}
.datepop .selectarea {
    float: left;
    height: auto;
    width: 640px;
    padding: 20px 15px 10px 0;
    max-height: 400px;
    overflow: auto;
}
.datepop .selectarea .classify-item .tit {
    border-top: 1px solid #E5E5E5;
    position: relative;
    margin-top: 7px;
    height: 1px;
}
.datepop .selectarea .classify-item .tit strong {
    display: block;
    position: absolute;
    background: #fff;
    padding: 0 5px;
    top: -11px;
    left: 0;
}
.datepop .selectarea .classify-item .checkitem {
  display: table;
  margin: 15px 0 0 40px;
}
.datepop .selectarea .classify-item .checkitem-li {
    float: left;
    width: 135px;
    margin-bottom: 10px;
}
.datepop .selectarea .classify-item .checkitem-li .checked {
    color: #13c4a5;
}
ul{
  -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 5px;
}
</style>


